@import url(https://fonts.googleapis.com/css2?family=Alef:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;1,100;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.Form {
  max-width: 40vw;
}

.Form div {
  margin-bottom: 2vh;
}

.ExpirationDate {
  display: flex;
  flex-direction: row;
}

@use './home';a{color:inherit;background-color:none;text-decoration:none}body{margin:0;padding:0;font:100% "Roboto",sans-serif}.popup-box{position:fixed;background:#f0d172;width:100%;height:100%;top:0;left:0;text-align:left;display:grid;grid-template-columns:1fr 0.9fr;grid-template-rows:0.7fr 2fr}.popup-box .beerName{color:white;font-size:60px;float:left;margin:0;margin-left:100px}.popup-box .category{color:white;font-size:30px;float:left;margin:0;margin-left:100px;line-height:70px}.box{position:absolute;margin:0 auto;height:40%;bottom:0;left:0;right:0;background:#fff;border-top-left-radius:100px;border-top-right-radius:100px;padding:50px 100px;overflow:auto;font-size:20px}.box h1{font-size:35px}.boxes{display:grid;grid-template-columns:1fr 1fr 1fr 1fr}.small-box{height:15vw;width:15vw;margin:5vw 0;background-color:rgba(240,209,114,0.17);border-radius:30px;text-align:center;display:grid}.small-box p{align-self:center;color:#f6c83f;font-size:15px;line-height:35px}.small-box p span{font-size:40px}.arrow{cursor:pointer;height:50px;transform:scaleX(-1);margin-top:75px;float:left;margin-left:100px}.cart{height:50px;float:right;margin-top:75px;justify-self:end;margin-right:100px}.beerTop{display:flex;flex-flow:column}.popup-img{grid-row:2;grid-column:2;height:35vw;border-radius:40px;z-index:1;justify-self:center}.close-icon{cursor:pointer;position:fixed;right:calc(15% - 30px);top:calc(100vh - 85vh - 33px);background:#ededed;line-height:20px;text-align:center;border:1px solid #999;font-size:16px;padding:5px}.beerCartWrapper{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-gap:1rem;padding-left:15px;padding-right:15px}.BeersList{display:flex;flex-flow:column}.beer-tap-img{order:-1;align-self:center;width:250px;height:300px;border-radius:22px;border:1px solid transparent;object-fit:cover}.beer-buttons{align-self:center;width:250px;padding:0px;margin-top:10px;margin-bottom:50px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;flex-flow:row-reverse;border:none;border-radius:22px;background-color:rgba(196,196,196,0.37)}.beer-buttons button{border:none;background-color:transparent;font-size:x-large}.beer-buttons input{border:none;background-color:transparent}.dec-btn{margin-bottom:4px}.beer-buttons-add-remove{padding:0px;display:flex;flex-direction:row;align-items:center;align-self:center}.beerListCenter{text-align:center}.Home{width:100%;height:100vh;background-color:#202632;font:100% "Alef",sans-serif;display:grid;grid-template-columns:1fr 3fr 1fr;justify-items:center}.Home .home-btn{color:#000;background-color:#e5e5e5;border-radius:57px;padding:10px 40px;border:none;text-decoration:none;text-transform:capitalize;font-weight:500;font-size:1.2rem;transition:all 0.3s ease-in-out;text-transform:uppercase;margin-top:50px;font-size:1.4em;margin-right:20px}.Home .home-btn:hover:hover{color:#202632;background-color:#f6c83f;transform:scale(1.1)}.Home .logo{height:auto;width:200px}.Home h1{color:#faf0e3;font-size:2em}.Home .home-wrapper{display:grid;grid-column:2/3;align-self:center;justify-items:center}.Home h1:nth-child(2){font-size:3em;margin-top:-20px;margin-bottom:10px}.Header{width:100%;height:100px;background-color:#f6c83f;color:#202632;display:flex;justify-content:space-between}.Header *{align-self:center}.Header h1{margin-left:50px}.Header .CartNotifications{margin-right:50px}.App{margin:0;padding:0}.App nav{position:fixed;bottom:0;width:100%;background-color:#faf0e3}.App nav ul{display:flex;width:100%;justify-content:space-around;padding:20px 0;margin:0}.App nav li{text-decoration:none;list-style:none;text-transform:uppercase;font-weight:bold}.CartNotifications .logo-dark{width:60px;height:auto;align-self:flex-end}

